import Point from "./point";

const MIN_MASS = 10, MAX_MASS = 30;
export default function PointManager(p, numPoints = 15, width, height){
    this.p = p; this.numPoints = numPoints
    this.dimensions = {x : width, y : height}
    this.points = []

    this.it = 0
    

    this.initialize = () => {
        this.points = []
        for(let i = 0; i < numPoints; i++){
            let mass = this.p.random(MIN_MASS, MAX_MASS)
            let x = this.p.random(0 + mass, this.dimensions.x - mass), y = this.p.random(0 + mass, this.dimensions.y - mass)
            this.points.push(new Point(this.p, x, y, mass))
        }
    }

    this.resize = (width, height) => {
        if(width){ this.dimensions.x = width}
        if(height){ this.dimensions.y = height  }
    }
    
    this.draw = (mouseOver) => { let lines = this.applyForces(mouseOver); this.drawLines(lines); this.stayInBounds(); this.drawPoints(); this.it ++}

    this.drawPoints = () => { this.points.forEach(pt => pt.draw())}

    this.applyForces = (mouseOver) => {
        var lines = []
        this.points.forEach((point, index) => {
            for(let i = index + 1; i < this.points.length; i++){
                let other = this.points[i]
                let force = { 
                    x: other.x - point.x,
                    y: other.y - point.y,
                    dist : ((other.x - point.x) ** 2 + (other.y - point.y) ** 2) ** 1.2
                }

                other.velocity.x += force.x / force.dist; other.velocity.y += force.y / force.dist
                point.velocity.x -= force.x / force.dist; point.velocity.y -= force.y / force.dist
                if(point.distance(other) < 100){
                    lines.push({
                        start : {x:point.x, y: point.y, radius:point.radius},
                        end : {x:other.x, y:other.y, radius:other.radius}
                    })
                }
            }


            if(mouseOver){
                let mx = this.p.mouseX, my = this.p.mouseY
                if(!mx || !my) { return}
                let force = {
                    x: mx - point.x,
                    y: my - point.y,
                    dist : ((mx - point.x) ** 2 + (my - point.y) ** 2) ** 0.85
                }
                point.velocity.x += force.x / force.dist; point.velocity.y += force.y / force.dist
            }
        })
        return lines
    }

    this.drawLines = (lines) =>{
        lines.forEach(line => {
            let startX = line.start.x, startY = line.start.y, endX = line.end.x, endY = line.end.y
            this.p.stroke('#333333')
            this.p.strokeWeight(2)
            this.p.line(startX, startY, endX, endY)
        })
    }

    this.stayInBounds = () => {
        this.points.forEach(point => {
            if(point.x < 0 + point.radius){ //left
                point.x = 0 + point.radius
                point.velocity.x *= -0.9
            }
            else if(point.x > this.dimensions.x - point.radius){ //right
                point.x = this.dimensions.x - point.radius
                point.velocity.x *= -0.9
            }

            if(point.y < 0 + point.radius){
                point.y = 0 + point.radius
                point.velocity.y *= -0.9
            }
            else if(point.y > this.dimensions.y - point.radius){ //right
                point.y = this.dimensions.y - point.radius
                point.velocity.y *= -0.9
            }
        })
    }
}