import React , {useState, useEffect} from 'react'
import './home.scss'
import Constellation from '../../Constellation/constellation';
import Button from '../../Components/Button';
import SpanText from '../../Components/SpanText';
import RadialNav from '../../Components/RadialNav/RadialNav';

const credentials = ['Design', 'Functionality', 'Stonks']
const NAME_SHORT = 'Dellon', NAME_FULL = 'Dellon Collins'

export default function Home(){
    const [name, setName] = useState()
    function nameOnResize(){ if(name != getName()) { setName(getName())} }
    function getName(){ return window.innerWidth > 600 ? NAME_FULL : NAME_SHORT; }
    function renderCredentials(){
        return credentials.map(cred => <div className="cred spanned-highlight" key={cred}>
            <SpanText text={cred}/></div>)
    }

    window.addEventListener('resize', () => { nameOnResize() })
    useEffect(() => { nameOnResize() })
    return (<>
        <Constellation/>
        <div className="home">
            <div className="salutation">
                <div className="greeting">Hi, my name's</div>
                <div className="name">{name}</div>
                <div className="pronun">pronunciation : [duh-lawn]</div>
            </div>
         
            <RadialNav/>

            <div className="credentials">
                {renderCredentials()}
            </div>
        </div>
    </>)
}