import React, {useState, useEffect} from 'react'
import P5Wrapper from 'react-p5-wrapper'
import sketch from './sketch'
import './constellation.scss'
export default function Constellation(){
    const [width, setWidth] = useState()
    const [height, setHeight] = useState()
    const [mouseOver, setMouseOver] = useState(false)
    function getContainer() {return document.getElementsByClassName('page-container')[0]}

    var getDimensions = () => {
        let el = getContainer()
        setWidth(el.clientWidth); setHeight(el.clientHeight)
    }
    window.onresize = ()=>{ getDimensions() }
    
    useEffect(() => {
        getDimensions()
        getContainer().onmouseenter = () => {setMouseOver(true)}
        getContainer().onmouseleave = () => {setMouseOver(false)}
    })
    return (
        <div className="constellation">
            <P5Wrapper sketch={sketch} width={width} height={height} mouseOver={mouseOver}/>
        </div>
    )
}