const SPEED_LIMIT = 3
export default function Point(p, x=0, y=0, mass=10, isVisible = true){
    this.p = p
    this.x = x; this.y = y; 
    this.mass = mass; this.radius = mass/2
    this.velocity = {x : 0, y : 0}
    this.isVisible = isVisible

    
}

Point.prototype.draw = function(){
    if(!this.isVisible) return
    // this.p.fill('#a8f1a122'); 
    // this.p.fill('rgba(245, 181, 192, 0.2)')
    this.p.fill('#353535'); 
    this.p.noStroke();
    this.p.ellipse(this.x, this.y, this.mass)
    this.x += this.velocity.x;
    this.y += this.velocity.y;

    if(this.velocity.y > SPEED_LIMIT) this.velocity.y = SPEED_LIMIT
    if(this.velocity.y < -SPEED_LIMIT) this.velocity.y = -SPEED_LIMIT
    if(this.velocity.x > SPEED_LIMIT) this.velocity.x = SPEED_LIMIT
    if(this.velocity.x < -SPEED_LIMIT) this.velocity.x = -SPEED_LIMIT
}

Point.prototype.distance = function(point){
    try{
        var dx = this.x - point.x;
        var dy = this.y - point.y;
        dx *= dx;
        dy *= dy;
        return Math.sqrt(dx + dy)
    }catch (e) {
        console.log(point, this)
        return 1000
    }
}