import React , {useState} from 'react'
import {Redirect} from 'react-router-dom'
import './radialnav.scss'
import CakeSvg from './CakeSvg';
import MenuBar from './MenuBar';

var radialButtons = [ 
    {direction : 'top', text : 'About'},
    {direction : 'right', text : 'Skills'},
    {direction : 'bottom', text : 'Contact'},
    {direction : 'left', text : 'Gallery'}
]
export default function RadialNav(){
    const [isActive, setIsActive] = useState(false)
    const [shouldRedirect, setShouldRedirect] = useState(false)
    const [redirectPath, setRedirectPath] = useState(null)
    var onNavClick = (event) => { setIsActive(!isActive) }
    var setN = (n) => { return {"--n" : n}}
    var setClassActive = () => (isActive ? " active" : " ")
    var renderRadialButtons = () => {
        return radialButtons.map((rb, index) => (
            <li className={rb.direction + " menu-item"} key={index} style={setN(index)}>
                <a ><span className="icon">{rb.text}</span></a>
            </li>
        ))
    }

    var renderPage = () => (<>
        <CakeSvg/>
        <ul className={"menu" + setClassActive()}>
            {renderRadialButtons()}
            <div className={"button" + setClassActive()} onClick={onNavClick}><MenuBar/></div>
        </ul>
    </>)

    return (
        <>
            {shouldRedirect ? <Redirect to={redirectPath}/> : null }
            {renderPage()}
        </>
    )
}