import React, {useState} from 'react'
import './nav.scss'
import NavLink from './NavLink';
import MenuButton from './MenuButton';

const LINKS = [
    {text : 'Home', route : '/home'},
    {text : 'About', route : '/about'},
    {text : 'Skills', route : '/skills'},
    {text : 'Gallery', route : '/gallery'},
    {text : 'Contact', route : '/contact'}
]
export default function NavLinks(){
    const [isMenuActive, setMenuActive] = useState(false)

    var getClassName = () => ("nav-links" + (isMenuActive ? ' active' : ''))
    var renderLinks = () => {
        return LINKS.map(link => <NavLink key={link.text} text={link.text} route={link.route}/>)
    }
    return (<React.Fragment>
        <div className={getClassName()}>
            {renderLinks()}
        </div>
        <MenuButton toggleActivity={() => setMenuActive(!isMenuActive) }></MenuButton>
    </React.Fragment>)
}