import React from 'react'
import './components.scss';



export default function SpanText({text}){
    function spanify(textString){
        return textString.split('').map((char, index) => <span key={index} style={{'--n': `${index}`}}>{char}</span>)
    }
    return ( 
        <>
            {spanify(text)}
        </>
    )
}